import { ok, bad } from "@/lib/api-respond";
import { createServerSupabaseClient } from "@/lib/supabase/server";
import { GeoJsonPoint, latLonFromPoint } from "@/lib/server/geo-utils";

type DriverStateRow = {
  driver_id: string;
  last_ping_at: string | null;
  loc: GeoJsonPoint | null;
  snapped_zone: number | null;
  speed_kmh: number | null;
  battery_pct: number | null;
  accuracy_m: number | null;
  recommendation: Record<string, unknown> | null;
  updated_at: string | null;
};

export async function GET() {
  let userId: string | null = null;
  try {
    const { supabase, user } = await createServerSupabaseClient();

    if (!user) {
      return bad("Please sign in", null, 401, "UNAUTHENTICATED");
    }

    userId = user.id;

    const { data, error, status } = await supabase
      .from("driver_state")
      .select(
        "driver_id,last_ping_at,loc,snapped_zone,speed_kmh,battery_pct,accuracy_m,recommendation,updated_at"
      )
      // driver_state rows are keyed by driver_id via RLS (auth.uid())
      .eq("driver_id", user.id)
      .maybeSingle();

    if (error) {
      if (status === 401) {
        return bad("Please sign in", null, 401, "UNAUTHENTICATED");
      }

      if (status === 403) {
        return bad("Forbidden", null, 403, "FORBIDDEN");
      }

      console.error("[driver-state] supabase query failed", {
        userId,
        status,
        error,
      });
      return bad("Failed to fetch driver state", null, 500, "DB_ERROR");
    }

    if (!data) {
      return ok(null);
    }

    const row = data as DriverStateRow;
    const coords = latLonFromPoint(row.loc);

    return ok({
      driver_id: row.driver_id,
      last_ping_at: row.last_ping_at,
      snapped_zone: row.snapped_zone,
      loc: row.loc,
      lat: coords.lat,
      lon: coords.lon,
      speed_kmh: row.speed_kmh,
      battery_pct: row.battery_pct,
      accuracy_m: row.accuracy_m ?? null,
      recommendation: row.recommendation,
      updated_at: row.updated_at,
    });
  } catch (error) {
    console.error("GET /api/driver-state failed", { userId, error });
    return bad("Failed to fetch driver state", null, 500, "DB_ERROR");
  }
}
